* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}
.container {
  width: 800px;
  height: 500px;
  display: flex;
}
.img-div {
  float: left;
  width: 350px;
  background-image: url("/public/BmiCalculator.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-size: contain;

}
.data {
  flex: 1;
}
.heading {
  margin-bottom: 20px;
  color: aqua;
  font-weight: 500;
  font-size: 20px;
  padding: 10px 0px;
  text-transform: uppercase;
}
.for-height,
.for-weight {
  margin-bottom: 5px;
}
.height-label,
.weight-label {
  display: block;
  margin-bottom: 5px;
  color: #888;
}
.height,
.weight {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 18px;
}
.btn {
  background-color: blue;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}
.btn:hover {
  background-color: blueviolet;
}
.result {
  border: 1px solid red;
  padding: 10px;
  border-radius: 5px;
  margin-top: 20px;
}
.result p:first-child {
  color: blue;
  padding: 10px 0px;
  font-weight: bold;
}
.error {
  color: red;
  margin-bottom: 5px;
  font-size: 14px;
}
.data button:nth-of-type(2) {
  background-color: rgba(255, 30, 0, 0.524);
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-left: 10px;
}
.data button:nth-of-type(2):hover {
  background-color: rgb(255, 0, 0);
}
